<template>
  <section>
    <div class='container centered' v-if="loading">
      <h3>Loading...</h3>
    </div>
    <div class='container centered' v-if="error">
      <h1>{{error}}</h1>
      <router-link to="/">Go Back</router-link>
    </div>
    <div v-if="profileData" class="container">
      <h1 class="gamertag">
          <img :src="profileData.platformInfo.avatarUrl || nameless" alt class="platform-avatar" />
          {{ profileData.platformInfo.platformUserHandle }}
        </h1>
      <div class="grid">
        <div>
          <img :src="this.random" alt />
        </div>
        <div>
          <ul>
            <li v-if="profileData.segments[1].stats.matchesPlayed">
              <h4>Matches Played</h4>
              <p>
                {{profileData.segments[1].stats.matchesPlayed.displayValue}}
                <span>({{profileData.segments[1].stats.matchesPlayed.percentile}})</span>
              </p>
            </li>
            <li v-if="profileData.segments[1].stats.wins">
              <h4>Wins</h4>
              <p>
                {{profileData.segments[1].stats.wins.displayValue}}
                <span>({{profileData.segments[1].stats.wins.percentile}}%)</span>
              </p>
            </li>
            <li v-if="profileData.segments[1].stats.kd">
              <h4>K/D Ratio</h4>
              <p>
                {{profileData.segments[1].stats.kd.displayValue}}
                <span>({{profileData.segments[1].stats.kd.percentile}}%)</span>
              </p>
            </li>
            <li v-if="profileData.segments[1].stats.eliminations">
              <h4>Lifetime Eliminations</h4>
              <p>
                {{profileData.segments[1].stats.eliminations.displayValue}}
                <span>({{profileData.segments[1].stats.eliminations.percentile}}%)</span>
              </p>
            </li>
            <li v-if="profileData.segments[1].stats.mostTimeSpentOnFire">
              <h4>Most Time Spend on Fire</h4>
              <p>
                {{profileData.segments[1].stats.mostTimeSpentOnFire.displayValue}}
                <span>({{profileData.segments[1].stats.mostTimeSpentOnFire.percentile}}%)</span>
              </p>
            </li>
          </ul>
        </div>
      </div>
      <router-link to="/">Go Back</router-link>
    </div>
  </section>
</template>

<script>
import axios from "axios";
const mei = require("./../assets/mei.png");
const ashe = require("./../assets/ashe1.png");
const genji = require("./../assets/genji.png");
const mccree = require("./../assets/mccree.png");
const mercy = require("./../assets/mercy.png");
const reinhardt = require("./../assets/reinhardt.png");
const tracer = require("./../assets/tracer.png");
const widow = require("./../assets/widow.png");
const lucio = require("./../assets/lucio.png");
const bastion = require("./../assets/bastion.png");
const nameless = require("./../assets/nameless.png");
const overwatchHeroList = [
  ashe,
  mei,
  genji,
  mccree,
  mercy,
  reinhardt,
  tracer,
  lucio,
  bastion,
  widow
];
let chooseRandom = array => {
  return array[Math.floor(Math.random() * array.length)];
};
export default {
  name: "Profile",
  data() {
    return {
      loading: false,
      error: null,
      profileData: null,
      random: "",
      nameless: nameless,
      chooseRandom(array) {
        return array[Math.floor(Math.random() * array.length)];
      }
    };
  },
  beforeCreate() {
    // document.body.className = "body-bg-no-image";
  },
  async created() {
    this.random = this.chooseRandom(overwatchHeroList);
    this.loading = true;
    try {
      const res = await axios.get(
        `/api/v2/profile/${this.$route.params.platform}/${this.$route.params.gamertag}`
      );

      this.profileData = res.data.data;
      console.log(this.profileData);
      this.loading = false;
    } catch (err) {
      this.loading = false;
      this.error = err.response.data.message;
    }
  }
};
</script>

<style scoped>
.container {
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  max-width: 700px;
  margin: 1rem auto;
  padding: 2rem 1.5rem;
  border-radius: 20px;
}
h1.gamertag {
  font-size: 2rem;
  background: rgba(0, 0, 0, 0.6);
  padding: 0.3rem 0.5rem;
  text-align: center;
  border-radius: 20px;
  margin-bottom: 3rem;
  display: flex;
  align-items: center;
}
a {
  display: inline-block;
  margin-top: 2rem;
  border: #fff 2px solid;
  padding: 0.5rem 0.8rem;
}
a:hover {
  border: #ccc 2px solid;
  color: #ccc;
}
.platform-avatar {
  width: 40px;
  margin-right: 0.7rem;
}
img {
  width: 100%;
}
.centered {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
}
li {
  background: rgba(0, 0, 0, 0.6);
  padding: 1rem;
  margin-bottom: 0.7rem;
  border-radius: 10px;
}
li p {
  font-size: 2rem;
}
li:first-child p {
  font-size: 1.5rem;
}
li span {
  font-size: 1rem;
  color: #ccc;
}
@media (max-width: 500px) {
  h1 {
    font-size: 1.5rem;
    display: block;
    text-align: center;
  }
  .platform-avatar {
    display: none;
  }
  .grid {
    grid-template-columns: 1fr;
  }
  li p {
    font-size: 1.5rem;
  }
}
</style>